import axios from "axios";
import { useState, useEffect, useContext } from "react";
import { Link, useNavigate } from "react-router-dom";
import Pagination from "react-js-pagination";
import { AuthContext } from "../../context/AuthProvider";
import "../../css/bbslist.css";
import "../../css/pageable.css";


function BbsList() {

	const [tradeBoardDtos, setTradeBoardDtos] = useState([]);
	const { auth, setAuth } = useContext(AuthContext)

	const token = localStorage.getItem("bbs_access_token");
	
	const [searchVal, setSearchVal] = useState("");


	const [pageable, setPageable] = useState(1);
	const [totalCnt, setTotalCnt] = useState(0);

	
	let navigate = useNavigate();

	const getBbsList = async (search, pageable) => {
		await axios.get(`${process.env.REACT_APP_SERVER_URL}/plant-service/api/write`, { params: { "search": search, "page": pageable-1 }, headers: {
			Authorization: `Bearer ${token}`,
		  } })
			.then((resp) => {
				console.log("[BbsList.js] useEffect() success :D");
				console.log(resp.data);
				console.log(pageable);
				setTradeBoardDtos(resp.data.content);
				setTotalCnt(resp.data.totalElements);
			
			})
			.catch((err) => {
				console.log("[BbsList.js] useEffect() error :<");

				// // console.log(""err);
				// alert("  ");

				
			});
	}

	useEffect(() => {
		getBbsList("",  1);
	}, []);


	
	const changeSearch = (event) => { setSearchVal(event.target.value); }
	const search = () => {
		console.log("[BbsList.js searchBtn()] searchVal=" + searchVal);

		navigate("/bbslist");
		getBbsList(searchVal, 1);
	}

	const changePage = (pageable) => {
		console.log(pageable);
		setPageable(pageable);
		getBbsList(searchVal, pageable);
	}

	return (

		<div>

			{ /*  */}
			<table className="search">
				<tbody>
					<tr>
					
						<td>
							<input type="text" className="form-control" placeholder=",  " value={searchVal} onChange={changeSearch} />
						</td>
						<td>
							<button type="button" className="btn btn-outline-secondary" onClick={search}><i className="fas fa-search"></i> </button>
						</td>
					</tr>
				</tbody>
			</table><br />

			<table className="table table-hover">
				{/* <thead>
					<tr>
						<th className="col-1"></th>
						<th className="col-6"></th>
						
						<th className="col-2"> </th>
						<th className="col-2"></th>
						<th className="col-1"></th>
						<th className="col-1"></th>
						
					</tr>
				</thead> */}

				<div className="card-list">
					{
						  tradeBoardDtos.map((tradeBoardDto, idx) => (
							<div className="card" key={tradeBoardDto.id}>
							<div className="card-header">
							  <Link to={`/bbsdetail/${tradeBoardDto.id}`}>
								<h5 className="card-title">{tradeBoardDto.title}</h5>
							  </Link>
							</div>
							<div className="card-body">
							  {/* <p className="card-status">: {tradeBoardDto.status}</p> */}
							  <p className="card-author"> : {tradeBoardDto.createBy}</p>
							  <p className="card-author"> : {tradeBoardDto.price} </p>
							  <p className="card-views"> : {tradeBoardDto.view}</p>
							  <p className="card-likes"> {tradeBoardDto.goodCount}</p>
							</div>
						  </div>
						))
						}
					
				</div>
			</table>

			<Pagination className="pagination"
				activePage={pageable}
				itemsCountPerPage={10}
				totalItemsCount={totalCnt}
				pageRangeDisplayed={5}
				prevPageText={""}
				nextPageText={""}
				onChange={changePage} />
				
			<div className="my-5 d-flex justify-content-center">
				<Link className="btn btn-outline-secondary" to="/bbswrite"><i className="fas fa-pen"></i> &nbsp; </Link>
			</div>

		</div>
	);
}

