import React, { useEffect, useState, useContext } from "react";
import axios from "axios";
import { useParams, useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";
import CommentWrite from "../comment/CommentWrite";
import CommentList from "../comment/CommentList";
import { AuthContext } from "../../context/AuthProvider";
import Comment from "../comment/Comment";
import { HttpHeadersContext } from "../../context/HttpHeadersProvider";
import "../../css/BbsDetail.css";
import ImageGalleryComponent from "./ImageGalleryComponent";

function BbsDetail() {
    const { auth, setAuth } = useContext(AuthContext);
    const { headers, setHeaders } = useContext(HttpHeadersContext);
    const [tradeBoardDto, setTradeBoardDto] = useState({});
    const { id } = useParams();
    const token = localStorage.getItem("bbs_access_token");
    const [tradeId, setTradeId] = useState(0);

    const [view, setView] = useState(0);
    const [replyList, setReplyList] = useState([]);

    const [memberId, setMemberId] = useState(0);
	// const [chatNo, setChatNo] = useState(0);
    const [price, setPrice] = useState();
    const [goodStatus, setGoodStatus] = useState(false);
    const navigate = useNavigate();

    const [imageUrls, setImageUrls] = useState([]);
    const [showImage, setShowImage] = useState(false);
    const [chatButtonText, setChatButtonText] = useState("");
    const [status, setStatus] = useState("");

    function formatDate(dateString) {
        const date = new Date(dateString);
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, "0");
        const day = String(date.getDate()).padStart(2, "0");
        const hours = String(date.getHours()).padStart(2, "0");
        const minutes = String(date.getMinutes()).padStart(2, "0");
        return `${year}/${month}/${day} ${hours} ${minutes}`;
    }

    const getImageUrls = async () => {
        await axios
            .get(`${process.env.REACT_APP_SERVER_URL}/plant-service/api/${id}/images`, { headers })
            .then((resp) => {
                console.log("[image.js] getImageUrls() success :D");
                console.log(resp);
                const imageUrls = resp.data.map((item) => item.url);

                console.log(imageUrls);
                setImageUrls(imageUrls);
            })
            .catch((err) => {
                console.log("[BbsDetail.js] getImageUrls() error :<");
                console.log(err);
            });
    };

    const getBbsDetail = async () => {
        await axios
            .get(`${process.env.REACT_APP_SERVER_URL}/plant-service/api/list/${id}`, { headers })
            .then((resp) => {
                console.log("[BbsDetail.js] getBbsDetail() success :D");
                console.log(resp.data);

                setTradeBoardDto(resp.data);
                setTradeId(resp.data.id);
                setView(resp.data.view); //   
                setMemberId(resp.data.memberId);
                setStatus(resp.data.status);
                setPrice(resp.data.price);
                console.log(tradeId);
                console.log(memberId);
                console.log(auth);
                if (auth == resp.data.createBy) {
                    console.log("ssss");
                    setChatButtonText("  ");
                } 
                else { //      
                    setChatButtonText("");
                }
            })
            .catch((err) => {
                console.log("[BbsDetail.js] getBbsDetail() error :<");
                console.log(tradeBoardDto.id);
                console.log(err);
            });
    };
    const getBbsView = async () => {
        await axios
            .get(`${process.env.REACT_APP_SERVER_URL}/plant-service/api/read/${id}`, { headers })
            .then((resp) => {
                console.log("[BbsDetail.js] getBbsView() success :D");
                setView(resp.data); // view  
            })
            .catch((err) => {
                console.log("[BbsDetail.js] getBbsView() error :<");
                console.log(err);
            });
    };

    const deleteBbs = async () => {
        await axios
            .delete(`${process.env.REACT_APP_SERVER_URL}/plant-service/api/list/${id}`, { headers })
            .then((resp) => {
                console.log("[BbsDetail.js] deleteBbs() success :D");
                console.log(resp.data);

                alert("   :D");
                navigate("/bbslist");
            })
            .catch((err) => {
                console.log("[BbsDetail.js] deleteBbs() error :<");
                console.log(err);
            });
    };
    const handleLikeClick = async () => {
        try {
            if (!auth) {
                //      
                navigate("/login");
                return;
            }

            const goodsRequestDto = {
                memberId: localStorage.getItem("id"),
                tradeBoardId: tradeId,
            };

            if (!goodStatus) {
                const response = await axios.post(
                    `${process.env.REACT_APP_SERVER_URL}/plant-service/api/goods/${memberId}`,
                    goodsRequestDto,
                    { headers }
                );

                //      
                setGoodStatus(true);
                alert("  !");
                window.location.reload();
                console.log(response.data);
            } else {
                const response = await axios.post(
                    `${process.env.REACT_APP_SERVER_URL}/plant-service/api/goods/${memberId}`,
                    goodsRequestDto,
                    { headers }
                );
                //     
                setGoodStatus(false);
                alert(" !");
                window.location.reload();
            }
        } catch (error) {
            console.error("Error adding to wishlist:", error);
        }
    };
    const getReplyList = async () => {
        await axios
            .get(`${process.env.REACT_APP_SERVER_URL}/plant-service/api/comment/reply`, {
                params: { tradeBoardId: id },
                headers: {
                    Authorization: `Bearer ${token}`,
                },
            })
            .then((resp) => {
                console.log("[BbsDetail.js] getReplyList() success :D");
                console.log(resp.data);

                setReplyList(resp.data); //   
            })
            .catch((err) => {
                console.log("[BbsDetail.js] getReplyList() error :<");
                console.log(err);
            });
    };
    const getGoodsStatus = async () => {
        try {
            const response = await axios.get(`${process.env.REACT_APP_SERVER_URL}/plant-service/api/goods/status`, {
                params: { memberId: localStorage.getItem("id"), tradeBoardId: id },
                headers: {
                    Authorization: `Bearer ${token}`,
                },
            });

            if (response.data !== null) {
                console.log(response.data);
                setGoodStatus(true);
            } else {
                // response.data false ,   false 
                setGoodStatus(false);
            }
        } catch (error) {
            console.error("Error getting trade status:", error);
        }
    };


    useEffect(() => {
        getBbsDetail();
        getGoodsStatus();
        getBbsView();
        getReplyList();
        getImageUrls();
 

    }, []);
    const updateBbs = {
        id: tradeBoardDto.id,
        createBy: tradeBoardDto.createBy,
        title: tradeBoardDto.title,
        content: tradeBoardDto.content,
        status: tradeBoardDto.status,
        price: tradeBoardDto.price,
    };

    const parentBbs = {
        id: tradeBoardDto.id,
        title: tradeBoardDto.title,
    };
    const data = {
        tradeBoardId: tradeBoardDto.id,
        memberId: tradeBoardDto.memberId,
    };



//    
const handleChatClick = async () => {
    try {
        if(localStorage.getItem("id")==tradeBoardDto.memberId){
            const resp = await axios.get(`${process.env.REACT_APP_SERVER_URL}/plant-chat-service/chatroom/exist/seller` ,{
                params: { 
                    tradeBoardNo: Number(tradeId),
                    memberNo: Number(localStorage.getItem("id"))
                },
                headers: {
                    Authorization: `Bearer ${token}`,
                }
                
            }
        );
        console.log(resp);
            if (resp.data==false){
                navigate("/noChatRoomExist");
            }
            else{
                navigate(`/chatroom/${tradeId}`);
            }

        }
        else{
        const chatRequestDto = {
            tradeBoardNo: tradeId,
            createMember: Number(localStorage.getItem("id")),
        };
        const accessToken = `Bearer ${window.localStorage.getItem('bbs_access_token')}`;
        //    
        const response = await axios.post(`${process.env.REACT_APP_SERVER_URL}/plant-chat-service/chatroom`, chatRequestDto, {
            params: { memberNo: tradeBoardDto.memberId },
            headers: {
                Authorization: accessToken,
            },
        });
        console.log(response);
        const chatNo = response.data.data.chatNo;
        console.log(chatNo);
        navigate(`/chatroom/${chatNo}/${chatRequestDto.tradeBoardNo}`);

        
        
        }
    } catch (error) {
        console.error("Error creating chat room:", error);
    }
};


    return (
        <div>
            {status === "" && (
                <div className="modal-container">
                    <div className="modal-content">
                        <p style={{ color: "red", fontSize: "24px" }}>   </p>
                        <div className="my-3">
                            <Link className="btn btn-outline-secondary" to="/bbslist">
                                <i className="fas fa-list"></i> 
                            </Link>
                        </div>
                    </div>
                </div>
            )}
            <div className="my-3 d-flex justify-content-end">
                <span className="ml-2" style={{ padding: "0.5em" }}>
                    {tradeBoardDto.goodCount} 
                </span>
                <button className="btn btn-outline-info" onClick={handleLikeClick}>
                    <i className={`fas ${goodStatus ? "fa-check" : "fa-star"}`}></i>{" "}
                    {goodStatus ? "" : ""}
                </button>{" "}
                <Link className="btn btn-outline-secondary" onClick={handleChatClick}>{chatButtonText}</Link>

                {" "}
                {/*
                      
                */}
                {auth == tradeBoardDto.createBy ? (
                    <>
                        {/*    */}
                        <Link className="btn btn-outline-secondary" to="/bbsupdate" state={{ tradeBoardDto: updateBbs }}>
                            <i className="fas fa-edit"></i> 
                        </Link>{" "}
                        <button className="btn btn-outline-danger" onClick={deleteBbs}>
                            <i className="fas fa-trash-alt"></i> 
                        </button>
                    </>
                ) : null}
            </div>

            <table className="table table-striped">
                <tbody>
                    <tr>
                        <th></th>
                        <td>
                            {/*     */}
                            <button className="btn btn-outline-primary" onClick={() => setShowImage(!showImage)}>
                                {showImage ? " " : "   "}
                            </button>
                            {showImage && <ImageGalleryComponent imageUrls={imageUrls} />}
                        </td>
                    </tr>
                    <tr>
                        <th className="col-3"></th>
                        <td>
                            <span>{tradeBoardDto.createBy}</span>
                        </td>
                    </tr>

                    <tr>
                        <th></th>
                        <td>
                            <span>{tradeBoardDto.title}</span>
                        </td>
                    </tr>

                    <tr>
                        <th></th>
                        <td>
                            <span>{formatDate(tradeBoardDto.updatedAt || tradeBoardDto.createdAt)}</span>
                        </td>
                    </tr>

                    <tr>
                        <th></th>
                        <td>
                            <span>{tradeBoardDto.view}</span>
                        </td>
                    </tr>
                    <tr>
                        <th> </th>
                        <td>
                            <span>{status}</span>
                        </td>
                    </tr>

                    <tr>
                        <th></th>
                        <td>
                            <div style={{ maxHeight: "400px", overflowY: "auto" }}>{tradeBoardDto.content}</div>
                        </td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>
                            <span>{new Intl.NumberFormat().format(price)}</span>
                        </td>
                    </tr>
                </tbody>
            </table>

            <div className="my-3 d-flex justify-content-center">
                <Link className="btn btn-outline-secondary" to="/bbslist">
                    <i className="fas fa-list"></i> 
                </Link>
            </div>
            <br />
            <br />

            {/*    */}
            {auth ? ( //     
                <CommentWrite data={data} />
            ) : null}

            {/*    */}
            <CommentList id={id} />
            <div className="my-5">
                {replyList.map((reply, index) => (
                    <div key={index}>
                        {/*    */}
                        <Comment obj={reply} tradeBoardId={tradeBoardDto.createBy} />
                    </div>
                ))}
            </div>
        </div>
    );
}

export default BbsDetail;
